hAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"rethinking-async-js","title":"Rethinking Asynchronous JavaScript","description":"Effective asynchronous JavaScript means knowing various different patterns and weaving them together to write readable and understandable code. ","time":"6 hours, 22 minutes CC","teacher":{"alt":"Kyle Simpson","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"functional-js-lite","title":"Functional-Lite JavaScript","description":"Practical take on functional JavaScript so you can use techniques like pure functions, map/reduce/filter and recursion to improve your programming today!","time":"3 hours, 9 minutes CC","teacher":{"alt":"Kyle Simpson","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"svg-animation","title":"Advanced SVG Animation","description":"Make resolution-independent, scalable, complex animations for a myriad of use cases across the web with SVG","time":"2 hours, 55 minutes","teacher":{"alt":"Sarah Drasner","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"ember-2","title":"Ember 2.x","description":"Become a master in Ember.js and be well-suited to build complex and highly interactive apps with this course.","time":"8 hours, 46 minutes CC","teacher":{"alt":"Mike North","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"api-design-nodejs","title":"API Design in Node.js (using Express & Mongo)","description":"Learn to build a fully functional API with Node.js and have the skills to build your own APIs using the Express.js framework.","time":"10 hours, 53 minutes CC","teacher":{"alt":"Scott Moss","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"angular-components-es6","title":"Component-Based Architecture in AngularJS 1.x and ES6","description":"Master building web apps in Angular 1.x and ES6. This is the best angular training. After doing our course you will be able to build 1.x apps and transition to 2.","time":"6 hours, 8 minutes CC","teacher":{"alt":"Scott Moss","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"modern-web-apps","title":"Building Web Apps (with React, Ampersand, ES6 and Webpack)","description":"Everything you need to build and deploy a maintainable single page app. Build an app from scratch using React, Ampersand, ES6 (ES2015), and Webpack.","time":"8 hours, 36 minutes","teacher":{"alt":"Henrik Joreteg","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"meteor","title":"Meteor","description":"Built by a team of MIT engineers, Meteor is a full stack open source framework for building web and mobile apps in pure JavaScript.","time":"10 hours, 6 minutes","teacher":{"alt":"Chris Mather","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"unit-testing-javascript","title":"Unit Testing JavaScript (with CoffeeScript)","description":"Learn to write testable code, testing types and strategies that will increase your ability to test JavaScript on the client and server (Node.js).","time":"4 hours, 45 minutes","teacher":{"alt":"Justin Searls","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"asynchronous-javascript","title":"Asynchronous Programming in JavaScript (with Rx.js Observables)","description":"Learning how to build and manage asynchronous programs is perhaps the most important part of becoming an effective JavaScript programmer.","time":"9 hours, 38 minutes CC","teacher":{"alt":"Jafar Husain","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"organizing-javascript","title":"Organizing JavaScript Functionality","description":"Before you use a framework, learn how to organize the different bits of JavaScript (modularizing, decoupling, etc) with this advanced JavaScript course.","time":"5 hours, 6 minutes CC","teacher":{"alt":"Kyle Simpson","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"javascript-coercion","title":"Coercion in JavaScript","description":"Most developers cite coercion and the == operator as black magic. By learning coercion you'll learn important clarifications in style and readability of JS.","time":"3 hours, 1 minutes","teacher":{"alt":"Kyle Simpson","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"javascript-basics","title":"Introduction to JavaScript Programming","description":"Master JavaScript with our online training and learn one of the easiest languages to get started with and one of the most powerful.","time":"2 hours, 40 minutes CC","teacher":{"alt":"Kyle Simpson","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"ui-prototyping-framer","title":"UI Prototyping with Framer.js","description":"Use Framer Studio to design and build responsive interactions, explore advanced animation techniques, and build data-driven UI prototypes together.","time":"4 hours, 46 minutes","teacher":{"alt":"Jay Stakelon","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"angular-app-dev","title":"AngularJS 1.x Application Development","description":"Learn everything from naming conventions to organizing your app into services, directives and routing with examples and challenges to anchor the concepts.","time":"4 hours, 13 minutes","teacher":{"alt":"Lukas Ruebbelke","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"react-intro-to-flux","title":"React.js (with Introduction to Flux Architecture)","description":"Learn from experts how to use React and become a master at creating user interfaces by Facebook and Instagram.","time":"4 hours, 29 minutes","teacher":{"alt":"Ryan Florence","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"responsive-email","title":"Responsive HTML Email Design","description":"Learn the importance of HTML email and prove that building beautiful, responsive email campaigns isn’t as scary as you thought.","time":"4 hours, 51 minutes","teacher":{"alt":"Justine Jordan","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"javascript-jquery-dom","title":"Advanced JS Fundamentals to jQuery & Pure DOM Scripting","description":"Gain an advanced JavaScript techniques and an understanding of difficult JavaScript concepts like closure, type comparators, coercion and delete.","time":"8 hours, 58 minutes CC","teacher":{"alt":"Justin Meyer","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"smacss","title":"Scalable Modular Architecture for CSS (SMACSS)","description":"Make your CSS more maintainable and modular using Jonathan Snook's Scalable Modular Architecture for CSS (SMACSS) methodology.","time":"5 hours, 51 minutes","teacher":{"alt":"Jonathan Snook","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"js-fundamentals-to-functional","title":"JavaScript: From Fundamentals to Functional JS","description":"Solidify your knowledge of functional JavaScript programming, including objects, arrays, and prototypes.","time":"7 hours, 8 minutes CC","teacher":{"alt":"Bianca Gandolfo","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"jsnext-es6","title":"JS.Next: ES6 / ES2015","description":"Learn the latest standard of advanced JavaScript concepts, ES6 and it's new features like destructuring, let, spread, arrow functions and modules.","time":"5 hours, 28 minutes CC","teacher":{"alt":"Aaron Frost","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img