64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},{"id":"introduction-to-javascript-jquery","title":"Introduction to JavaScript and jQuery","description":"Karl Swedberg, author of Learning jQuery, teaches the JavaScript language; including closures, anonymous functions, and implicit versus explicit iteration.","time":"3 hours, 34 minutes","teacher":{"alt":"Karl Swedberg","src":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="},"img":"data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=="}]